













export default {
    data: {
        statusBarHeight: '',
		
    },
    created() {
        let _t = this;
        setTimeout(() => { //获取状态栏高度，setTimeout后才能调用uni.
            uni.getSystemInfo({
                success: function(res) {
                    _t.statusBarHeight = res.statusBarHeight;
                    console.log(_t.statusBarHeight);
                }
            });
        }, 1);
    },
	methods: {
		goWode() {
			console.log(123)
			uni.switchTab({
				url: '../wode'
			});
		}
	}
};



